<template>
  <div class="myteam">
    <section
      v-for="(char, index) in myTeam"
      :key="index"
      :class="{ none: char === false }"
    >
      <div class="myteam-char" v-if="char !== false">
        <CharFace :char="char" :index="index" />

        <section class="myteam-gamestatus">
          <div class="myteam-effects">
            <v-popover
              trigger="hover"
              v-for="(s) in skillQueue(index, myId)"
              :key="s.id"
            >
              <img class="myteam-effect puff-in-center" :src="`/img/game/${s.skillpic}.jpg`" />
              <template slot="popover">
                <ul>
                  <li>{{ s.skillName }}</li>
                  <li v-for="(message, key) in s.tips" :key="key">
                    <ul>
                      <li>{{ message.message }}</li>
                      <li>{{ message.duration }}</li>
                    </ul>
                  </li>
                  <li v-for="(message, key) in s.notifications" :key="key">
                    <ul>
                      <li>{{ message }}</li>
                    </ul>
                  </li>
                </ul>
              </template>
            </v-popover>

            <img
              class="myteam-effect"
              v-for="(s, key) in tempQueue(index)"
              @dblclick="removeFromTempQueue(s)"
              :key="key"
              :src="`/img/game/${skill(s.caster, s.skill).skillpic}.jpg`"
            />
          </div>

          <Skills
            :class="{ 'fade-out-left': !myData.isTurn }"
            :skills="char.skills"
            :charIndex="index"
            @selected-skill="setSkill"
          />
        </section>
      </div>
    </section>
  </div>
</template>

<script>
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watchers from "./script/watchers";

export default {
  data() {
    return {
      selectedSkill: {},
    };
  },
  components,
  computed,
  methods,
  watchers,
  created() {
    this.$eventBus.$on("set-target", (target) => {
      const room = this.$store.getters["game/getBattleRoom"];
      room.send("add-skill-to-queue", {
        ...this.selectedSkill,
        target: target,
      });
      this.$store.commit("game/setBusyStatus", true);
    });
  },
};
</script>

<style lang="scss">
@import "./style";
</style>